  --placeholder-txt-cl: #505050;
  --error-txt-cl: #ff705d;
  --green-btn-bg-cl: #bbf330;
  --green-btn-bg-hover-cl: #b5ff00;
  --border-btn-cl: #7eb101;
  --border-btn-hover-cl: #fff;
  --yellow-btn-bg-hover-cl: #ffe550;
  --icon-bg-color: #7eb101;
  --grey-bg-cl: rgba(217, 217, 217, .27);
  --yellow-bg-cl: #ffe550;
  --white-bg-cl: #fefefe;
  --btn-white-bg-cl: #fff;
  --header-shadow: 0px 4px 24px rgba(105, 105, 105, .1);
  --footer-shadow: 0px -4px 24px rgba(105, 105, 105, .1);
  --shadow-fruit-cl: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --border-btn-default: #ffe550;
  --border-bth-success: #7eb101;
  --border-bth-error: #ff705d;
  --arrow-box-shadow: 0px 4px 4px rgba(0, 0, 0, .06);
  --arrow-bg-cl: #2c2c2c;
  --arrow-bg-hover-cl: #7eb101;
  --border-yellow-cl: #ffe550;
  --border-green-cl: #bbf330;
  --timing-function: 250ms cubic-bezier(.4, 0, .2, 1);
}

